
.book {
  margin-top: 100px;
  height: 600px;
  overflow: hidden;
  position: relative;
}
.book .book-show-x {
  position: relative;
}
.book .book-show-x:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(31, 33, 35, 0.8);
  z-index: 1;
}
.book .book-show-x .book-bg {
  height: 140px;
  width: 7200px;
}
.book .book-show-x .book-bg .book-line {
  height: inherit;
}
.book .book-show-x .book-bg .book-line-one {
  background: url(63cbe639a970d966f0e401c4718bd4dd.jpg) repeat-x;
}
.book .book-show-x .book-bg .book-line-two {
  background: url(b1dd36f44b1891f02303735de5555829.jpg) repeat-x;
}
.book .book-show-x .book-bg .book-line-three {
  background: url(44c109c60e132aa7018eacb0a1f6c45a.jpg) repeat-x;
}
.book .book-bg-back {
  height: 140px;
  width: 100%;
  position: absolute;
  background: url(44c109c60e132aa7018eacb0a1f6c45a.jpg) repeat-x;
  z-index: -1;
}
.book .book-bg-back-two {
  margin-top: 140px;
}
.book .book-show {
  width: 100%;
  height: 530px;
  position: absolute;
  z-index: 2;
  margin-top: -100px;
  -webkit-transform-style: preserve-3d;
  -webkit-perspective: 1800px;
  transform-style: preserve-3d;
  perspective: 1800px;
}
.book .book-show .book-show-pic {
  width: 230px;
  height: 300px;
  position: absolute;
  left: calc(50% - 100px);
  transition: 2s ease-in;
}
